import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "../css/perfil.css";

const Perfil = ({ user }) => {
  const handleLogout = () => {
    // Lgica para cerrar sesin
    console.log("Cerrar sesin");
  };

  const handleViewPurchases = () => {
    // Lgica para ver compras realizadas
    console.log("Ver compras realizadas");
  };

  return (
    <div className="container mt-5 perfil-container">
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h2>Mi perfil</h2>
        <div>
          <button
            className="btn btn-outline-dark me-2"
            onClick={handleViewPurchases}
          >
            Ver Compras
          </button>
        </div>
      </div>

      <div className=" mb-4">
        <div className="card-body">
          <h5 className="card-title">Mi informacin </h5>
          <p className="card-text">
            <strong>Correo electrnico: </strong>
            {user.email}
          </p>
          <p className="card-text">
            <strong>Nombre: </strong>
            {user.firstName}
          </p>
          <p className="card-text">
            <strong>Apellido: </strong>
            {user.lastName}
          </p>
          <p className="card-text">
            <strong>Fecha de nacimiento: </strong>
            {user.birthDate}
          </p>
          <p className="card-text">
            <strong>Nmero de telfono: </strong>
            {user.phoneNumber}
          </p>
          <p className="card-text">
            <strong>Gnero: </strong>
            {user.gender}
          </p>
          <p className="card-text">
            <strong>Pas: </strong>
            {user.country}
          </p>
          <p className="card-text">
            <strong>Staff Card: </strong>
            {user.staffCard}
          </p>
        </div>
      </div>

      <div className=" mb-4">
        <div className="card-body">
          <h5 className="card-title">Configuracin de pago </h5>
          <p className="card-text">
            No hay ninguna tarjeta guardada actualmente. Puedes agregar nuevos
            mtodos de pago durante el proceso de compra.
          </p>
        </div>
      </div>

      <div className=" mb-4">
        <div className="card-body">
          <h5 className="card-title">Direccin </h5>
          <p className="card-text">
            Tambin puedes agregar y editar tus direcciones de entrega aqu
          </p>
          <p className="card-text">No se guard tu domicilio.</p>
        </div>
      </div>

      <div className=" mb-4">
        <div className="card-body">
          <h5 className="card-title">Privacidad</h5>
          <p className="card-text">
            <a href="#">Cambiar contrasea</a>
          </p>
          <p className="card-text">
            <a href="#">Ir a Mi portal de privacidad</a>
          </p>
        </div>
        <button className="btn mt-5 btn-outline-dark" onClick={handleLogout}>
          Cerrar Sesin
        </button>
        <button className="btn mt-5 btn-outline-dark" onClick={handleLogout}>
          Eliminar Cuenta
        </button>
      </div>
    </div>
  );
};

// Ejemplo de datos de usuario
const user = {
  email: "golomian16@hotmail.com",
  firstName: "Nombre",
  lastName: "Apellido",
  birthDate: "19/10/2003",
  phoneNumber: "Nmero de telfono",
  gender: "Gnero",
  country: "Mxico",
  staffCard: "Staff Card",
};

const App = () => <Perfil user={user} />;

export default App;
