import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import { Form, Button, Col, Row } from "react-bootstrap";
import "../css/registro.css";

const Registro = () => {
  return (
    <div className="container mt-5 mb-4">
      <h2 className="text-center">Vamos a hacerte un Xclusive member.</h2>
      <p className="text-center">
        Hemos enviado un cdigo a <strong>golomian72@hotmail.com</strong>
      </p>
      <Form>
        <Row className="mb-3">
          <Form.Group as={Col}>
            <Form.Label>Nombre*</Form.Label>
            <Form.Control type="text" placeholder="Nombre" />
          </Form.Group>

          <Form.Group as={Col}>
            <Form.Label>Apellidos*</Form.Label>
            <Form.Control type="text" placeholder="Apellidos" />
          </Form.Group>
        </Row>

        <Form.Group className="mb-3">
          <Form.Label>Correo*</Form.Label>
          <Form.Control type="email" placeholder="Correo" />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Contrasea*</Form.Label>
          <Form.Control type="password" placeholder="Contrasea" />
          <Form.Text className="text-muted">
            <ul>
              <li>8 caracteres como mnimo</li>
              <li>Letras maysculas, minsculas y un nmero</li>
            </ul>
          </Form.Text>
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Preferencia de compra*</Form.Label>
          <Form.Select>
            <option>Elige una opcin</option>
            <option>Ropa</option>
            <option>Calzado</option>
            <option>Accesorios</option>
          </Form.Select>
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Fecha de nacimiento*</Form.Label>
          <Form.Control type="date" />
          <Form.Text className="text-muted">
            Consigue una recompensa de Xclusive Member por tu cumpleaos.
          </Form.Text>
        </Form.Group>
        <Row className="mb-3">
          <Form.Group as={Col}>
            <Form.Label>Codigo Postal</Form.Label>
            <Form.Control type="number" placeholder="Codigo Postal" />
          </Form.Group>

          <Form.Group as={Col}>
            <Form.Label>Estado</Form.Label>
            <Form.Control type="text" placeholder="Estado" />
          </Form.Group>
          <Form.Group as={Col}>
            <Form.Label>Municipio o Alcaldia</Form.Label>
            <Form.Control type="text" placeholder="Municipio" />
          </Form.Group>
        </Row>
        <Row className="mb-3">
          <Form.Group as={Col}>
            <Form.Label>Colonia</Form.Label>
            <Form.Control type="text" placeholder="Colonia" />
          </Form.Group>

          <Form.Group as={Col}>
            <Form.Label>Calle</Form.Label>
            <Form.Control type="text" placeholder="Calle" />
          </Form.Group>

          <Form.Group as={Col}>
            <Form.Label>Numero</Form.Label>
            <Form.Control type="number" placeholder="Numero" />
          </Form.Group>
        </Row>
        <Form.Group className="mb-3" controlId="formBasicCheckbox1">
          <Form.Check
            type="checkbox"
            label="Regstrate para recibir correos electrnicos y no perderte las actualizaciones de productos, ofertas y ventajas para Members."
          />
        </Form.Group>

        <Form.Group className="mb-3" controlId="formBasicCheckbox2">
          <Form.Check
            type="checkbox"
            label="Acepto la Poltica de privacidad y los Trminos de uso de Xclusive Store."
          />
        </Form.Group>

        <Button variant="dark" type="submit" className="w-100">
          Crear una cuenta
        </Button>
      </Form>
    </div>
  );
};

export default Registro;
